<template>
    <div class="box" id="box" style="overflow-y: hidden;">
        <div class="head">
            <h1 v-for="(item,index) in head.title1" :key="index">{{item}}</h1>
            <h3 style="word-break:keep-all;">{{head.title2}}</h3>
            <h3 style="padding-top: 0px;"><span style="font-weight: 600;">Index Terms </span>
                &nbsp;&nbsp;&nbsp;&nbsp;{{head.title3}}</h3>
        </div>
        <div class="main">
            <img style="width: 100%;" :src="img" alt="">
            <img style="width: 100%;margin-top: 24px;" :src="img1" alt="">
        </div>
        <div class="bottom">
            <div style="display: flex;justify-content: space-between;">
                <span class="bottominfo" @click="gotodetail">{{bottomleft.name}}</span>
                <span v-if="bottomright.yulan" class="bottominfo" @click="download">{{bottomright.name}}</span>
                <a v-else href="HarryPotter.zip" download="HarryPotter.zip" class="bottominfo">{{bottomright.name}}</a>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "DialogInfo",
        props: {
            head: Object,
            title1: Array,
            title2: String,
            title3: String,
            img: String,
            img1: String,
            bottomleft: Object,
            bottomright: Object
        },
        data() {
            return {
                obj: ["A Design Space for", "Applying the Freytag's Pyramid Structure to", "Data Stories"],
                title4: `Data stories integrate compelling visual content to communicate data insights in the form of narratives. The narrative structure of a data story serves as the backbone that determines its expressiveness, and it can largely influence how audiences perceive the insights. Freytag's Pyramid is a classic narrative structure that has been widely used in film and literature. While there are continuous recommendations and discussions about applying Freytag's Pyramid to data stories, little systematic and practical guidance is available on how to use Freytag's Pyramid for creating structured data stories. To bridge this gap, we examined how existing practices apply Freytag's Pyramid by analyzing stories extracted from 103 data videos. Based on our findings, we proposed a design space of narrative patterns, data flows, and visual communications to provide practical guidance on achieving narrative intents, organizing data facts, and selecting visual design techniques through story creation. We evaluated the proposed design space through a workshop with 25 participants. Results show that our design space provides a clear framework for rapid storyboarding of data stories with Freytag's Pyramid.`,
                title5: "Freytag's Pyramid, Narrative Structure, Narrative Visualization, Data Storytelling, Data Video",
                imgurl: require("../assets/6-03.png"),
                title6: "Web",
                title7: "Paper",
                Psrc: ''
            }
        },
        methods: {
            gotodetail() {
                window.open(this.bottomleft.url)
            },
            download() {
                window.open('pdf/web/paper.pdf', 'tb.icon')
            }
        },
        directives: {
            down: {

                inserted: (el, binding) => {
                    el.style.cssText = 'cursor: pointer;color:write;'
                    el.addEventListener('click', () => {
                        console.log(binding.value)
                        let link = document.createElement('a')
                        let url = baseDownloadUrl + binding.value
                        // 这里是将url转成blob地址，
                        fetch(url).then(res => res.blob()).then(blob => { // 将链接地址字符内容转变成blob地址
                            link.href = URL.createObjectURL(blob)
                            console.log(link.href)
                            link.download = ''
                            document.body.appendChild(link)
                            link.click()
                        })
                    })
                }


            }
        }
    }
</script>

<style>
    h1 {
        font-size: 42px;
        margin: 10px 0;
    }

    .head {
        color: #1A1A1A;
    }

    .main {
        margin-top: 72px;
    }

    h3 {
        padding-top: 26px;
        margin-bottom: 24px;
        font-weight: normal;
        font-size: 16px;
        line-height: 26px;
    }

    .box {
        /* width: 1219px; */
        margin: 0 auto;
        text-align: left;
        font-family: Verdana;
    }

    .bottom {

        margin: 36px 0 0px 0;
    }

    .bottominfo {
        text-decoration: underline;
        color: #FD7210;
        font-weight: 600;
        font-size: 22px;
        cursor: pointer;
    }
    .el-dialog__body{
        --el-dialog-padding-primary: 60px
    }
    .el-dialog__header{
        padding: 0px !important;
    }
</style>